footer{
    background-color: var(--brand_dark);
}
.footer-container {
    display: flex;
    max-width: var(--contWidth);
    margin: 0 auto;
    padding: 50px 0;
    overflow: hidden;
    color: #fff;
    padding:40px 20px;
      .right-container {
        flex: auto;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        &::before{
            content: attr(data-content);
            display: flex;
            position: relative;
            flex: 0 1 100%;
            font-size: 30px;
            font-weight: 600;
            margin-bottom: 20px;
        }
        .quick-actions-list-container {
            position: relative;
            display: flex;
            flex-direction: column;
            flex: 1 0 25%;
            max-width: 220px;
            .quick-actions-heading {
                position: relative;
                font-size: 16px;
                &:before,&:after{
                    content: "";
                    position: absolute;
                    top: 50%;
                    right: 16px;
                    width: 16px;
                    height: 1px;
                    background: #444444;
                    opacity: 0;
                    transform: rotate(0deg);
                    transition: transform 350ms ease-in-out;
                }
                &:after{
                    transform: rotate(90deg);
                }
              }
              .quick-actions-list {
                list-style: none;
                padding: 0;
                font-size: 12px;
                max-height: 1000px;
                overflow: hidden;
                transition: max-height 350ms ease-in-out;
                a{
                    text-decoration: none;
                    color: #fff;
                }
                li {
                    padding: 6px 0;
                    white-space: nowrap;
                  }
              }
          }
      }
      .left-container {
        width: auto;
        border-right: 1px rgba(238, 238, 238, 0.185) solid;
        margin-right: 40px;
        max-width: 20%;
        h3{
            margin: 15px 0;
        }
        .social{
            a{
                margin-right: 20px;
                img{
                    width: 20px;
                }
            }
        }
        .contact-footer, .about-brand{
            padding-bottom: 10px;
        }
      }
  }