/* header section */
.header-container {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 90px;
    background-color: #ffffff;
    border-bottom: 1px solid #e7e7e7;
    z-index: 99;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
    .header-content {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      max-width: var(--contWidth);
      margin: 0 auto;
      padding: 0 20px;
      .header-brand {
        display: flex;
        align-items: center;
        height: 100%;
        a {
          height: 100%;
          display: flex;
          .brand {
            margin: auto 0;
          }
        }
        .hamburger-btn {
          position: absolute;
          display: none;
          background: url("./../../assets//icons/nav-menu.svg") no-repeat center center;
          background-size: 24px 24px;
          height: 56px;
          min-width: 46px;
          border: none;
          padding: 0;
          margin-left: -12px;
          outline: none;
          z-index: 2;
        }
      }
      .header-main {
        flex: 1;
        display: flex;
        padding-left: 5%;
        .header-main-actions-container {
          display: inline-flex;
          flex: 1;
          .header-main-actions-list-content {
            display: flex;
            flex-direction: row;
            flex: 1;
            width: 100%;
          }
          .actions-list-container {
            position: relative;
            display: inline-flex;
            color: #393939;
            &:hover{
              &:before{
                content: "";
                height: 60px;
                width: 100%;
                position: absolute;
                top: 18px;
              }
            }
          }
        }
      }
    }
  }
  
  
  
  .actions-list-container .actions-heading,
  .header-right .actions-heading {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    cursor: pointer;
    letter-spacing: 0.2px;
    font-weight: 800;
    white-space: nowrap;
    padding: 24px 12px;
    margin-right: 30px;
  }
  
  .actions-list-container .actions-heading::after {
    content: "";
    position: absolute;
    right: -4px;
    top: 28px;
    height: 10px;
    width: 10px;
    background: url("./../../assets/icons/arrow.svg") no-repeat center center;
    background-size: contain;
    transform: rotate(90deg);
    filter: brightness(0%) contrast(100%);
    transition: transform 0.2s ease-in;
  }
  
  .header-right .actions-heading {
    padding: 6px 12px 0 12px;
    font-size: 12px;
    margin: 0;
    height: 100%;
  }
  
  .actions-heading img {
    height: 19px;
    width: 19px;
  }
  
  .actions-list-container .actions-list {
    display: none;
    position: absolute;
    transition: all 0.7s ease 0s;
    top: 78px;
    left: -36px;
    max-width: 350px;
    min-width: 240px;
    border-radius: 0px;
    box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.05);
    background-color: #fff;
    border-radius: 0 0 10px 10px;
    overflow: hidden;
    padding: 0;
  
    li {
      display: flex;
      a {
        padding: 12px 36px;
        font-size: 14px;
        width: 100%;
        white-space: nowrap;
        text-decoration: none;
        color: #333;
      }
      &:hover {
        background-color: var(--brand);
        a{
          color: var(--white);
        }
      }
    }
  }
  
  .header-content .header-right {
    display: flex;
    height: 100%;
    margin-left: auto;
  
    .actions-heading {
      margin-right: 0;
    }
  }
  



  @media (min-width: 768px) {
    .actions-list-container:hover .actions-list {
      display: block;
      animation: header-actions-enter 0.2s ease-in;
    }
  
    .actions-list-container:hover .actions-heading {
      color: var(--brand);
    }
  
    .actions-list-container:hover .actions-heading::after {
      transform: rotate(-90deg);
      filter: none;
    }
  
    .actions-list-container .actions-heading.no-arrow::after,
    .actions-list-container:hover .actions-heading.no-arrow::before,
    .actions-list-container:hover .actions-heading.no-arrow {
      content: none;
      color: #393939;
    }
  
    @keyframes header-actions-enter {
      0% {
        transform: translateY(20px);
        opacity: 0;
      }
  
      100% {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }