.banner{
    background-color: var(--light);
    padding-top: 92px;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
    margin-bottom: -30px;
    z-index: 1;
    position: relative;
    .banner-content{
        min-height: 500px;
        .content{
            margin-right: 80px;
        }
    }
    .action{
        width: 390px;
        text-align: center;
        button{
            background-color: var(--brand);
            border-radius: 40px;
            width: 300px;
            padding: 15px;
            border: navajowhite;
            margin-bottom: 15px;
            color: #fff;
            font-size: 20px;
        }
    }
}