body {
  margin: 0;
  padding: 0;
  // font-family: 'helveticaneuecyrlight',"Helvetica Neue", Helvetica, Arial, sans-serif;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 100;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4;
  background-color: var(--white);
  @media screen and (max-width: 767px) {
    font-weight: 300;
   }
}

code {
  font-family: Helvetica, Arial, sans-serif;
}
:root {
  --brand: #3a63e8;
  --brand_dark: #303565;
  --white: #ffffff;
  --light: #e9effc;
  --breakpoint-xs: 0;
  --contWidth:1180px;
  --breakpoint_sm: 576px;
  --breakpoint_md: 768px;
  --breakpoint_lg: 992px;
  --breakpoint_xl: 1200px;
  --headerHeight:80px;
  --headerHeightInner:60px;
  --headerHeightxs:50px;
  --headerHeightsticky:70px;
  --headerHeightstickyxs:60px;
  --font_family_sans_serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font_family_monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
*{
  box-sizing: border-box;
}

ul, h1, h2, h3, h4, p{
  padding: 0;
  margin: 0;
}

button{
  outline:none !important;
  cursor: pointer;
}